	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Finding The Door","date":"24 Mar 2002","slug":"/2002-03-24-finding-the-door/","tags":"Y2002"},"html":"<p>Christopher's new word this weekend is 'lade', pronounced LAW-deh. As in the last part of 'Schokolade', German for chocolate. </p>\n<p>I haven't been able to find the answer myself, and <a href=\"http://radio.userland.com/discuss/msgReader$12317?mode=topic&amp;y=2002&amp;m=3&amp;d=23\">my post</a> on the Radio discussion list has gone unanswered, so I'll try here: how can I download my Manila site using the current version of Radio? The <a href=\"http://radio.userland.com/manilaSiteConverterTool\">ManilaSiteConverter</a> and <a href=\"http://www.thetwowayweb.com/stories/storyReader$50\">TheXmlFiles</a> tools do not work. Or must I use XML-RPC, or work with the <a href=\"http://manilanewbies.userland.com/stories/storyReader$1019\">downloadable mysite.root database</a>?</p>"},"next":{"fields":{"title":"Talking Vegetables","date":"23 Mar 2002","slug":"/2002-03-23-talking-vegetables/"}},"previous":{"fields":{"title":"If you can't beat 'em","date":"25 Mar 2002","slug":"/2002-03-25-if-you-cant-beat-em/"}}},{"node":{"fields":{"title":"Talking Vegetables","date":"23 Mar 2002","slug":"/2002-03-23-talking-vegetables/","tags":"Y2002"},"html":"<p>It's Saturday, Christopher could sense it, and woke us up early. MamaMaus sent us boys downstairs so she could get back to sleep. There is no milk or Brotchen in the house, so breakfast is delayed until the bakery/grocery store opens.</p>\n<p>Mark Pilgrim has switched <a href=\"http://diveintomark.org\">diveintomark</a> to <a href=\"http://www.moveabletype.org\">MT2</a>, and was also able to <a href=\"http://diveintomark.org/archives/2002/03/22.html#movable_type\">import his old GreyMatter and Manila posts</a>. If he can do it, so can I, right? Well, I hope he posts he script he used. I'm too lazy, er, I mean busy to come up with my own. :-)</p>"},"next":{"fields":{"title":"Resuming Transmission","date":"21 Mar 2002","slug":"/2002-03-21-resuming-transmission/"}},"previous":{"fields":{"title":"Finding The Door","date":"24 Mar 2002","slug":"/2002-03-24-finding-the-door/"}}},{"node":{"fields":{"title":"Resuming Transmission","date":"21 Mar 2002","slug":"/2002-03-21-resuming-transmission/","tags":"Y2002"},"html":"<p>Look for some changes here in the next couple of weeks.</p>"},"next":{"fields":{"title":"Dog Food","date":"21 Mar 2002","slug":"/2002-03-21-dog-food/"}},"previous":{"fields":{"title":"Talking Vegetables","date":"23 Mar 2002","slug":"/2002-03-23-talking-vegetables/"}}}],"pathPrefix":"","first":false,"last":false,"index":914,"pageCount":1101}}

